import React from 'react';
import '../styles/header.scss'

//I used class component because maybe i will try to add to header changeable background so i leave state.

class Header extends React.Component {
    state = {
        nothing: 'nothing',
    }
    render(){
        return(
            <div className='header'>
            <h1 className='header__title'>Some nice looking title</h1>
            <div className="header__area"></div>
            <p className='header__description'>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Necessitatibus sunt placeat veniam, velit tenetur voluptatum voluptatibus in laudantium. Error magnam quia assumenda eius aspernatur tempore cum ipsam velit enim nostrum?
              </p>
            <p className='header__slogan'>A few nice catchy words</p>
            </div>        
            )
    }
}
export default Header;
