import React from 'react';
import '../styles/icons.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee, faBed, faBowlingBall, faFighterJet, faSmileBeam, faSnowboarding } from '@fortawesome/free-solid-svg-icons'

const Icons = () => {
    return (  
        <section className='icons'>
            <div className='icons__simple icons__simple--one'><FontAwesomeIcon icon={faSnowboarding}/></div>
            <div className='icons__simple icons__simple--two'><FontAwesomeIcon icon={faCoffee}/></div>
            <div className='icons__simple icons__simple--three'><FontAwesomeIcon icon={faBed}/></div>
            <div className='icons__simple icons__simple--four'><FontAwesomeIcon icon={faBowlingBall}/></div>
            <div className='icons__simple icons__simple--five'><FontAwesomeIcon icon={faFighterJet}/></div>
            <div className='icons__simple icons__simple--six'><FontAwesomeIcon icon={faSmileBeam}/></div>
        </section>
    );
}
 
export default Icons;