import React from 'react';
import '../styles/footer.scss'

const Footer = () => {
    return (
        <section className='footer'>
            <form className='footer__subscription subscription'>
                <p className='subscription__caption'>Subscribe to our communication</p>
                <input className='subscription__input' type='email' placeholder='Email address'></input>
            </form>
            <section className='footer__customer customer'>
                <p className='customer__caption'>Customer</p>
                <ul className='customer__list'>
                    <li className='customer__item'>Contact US</li>
                    <li className='customer__item'>FAQs </li>
                    <li className='customer__item'>Privacy policy</li>
                </ul>
            </section>
            <section className='footer__company company'>
                <p className='company__caption'>Company</p>
                <ul className='company__list'>
                    <li className='company__item'>Press</li>
                    <li className='company__item'>Our Story</li>
                    <li className='company__item'>Careers</li>
                </ul>
            </section>
            <section className='footer__social social'>
                <p className='social__caption'>Social Medias</p>
                <ul className='social__list'>
                    <li className='social__item '>Facebook</li>
                    <li className='social__item'>Insatgram</li>
                    <li className='social__item'>Twitter</li>
                </ul>
            </section>
            <section className='footer__sign'>Created by Rafał Fuz</section>
        </section>
      
     );
}
 
export default Footer;